import * as React from "react";
import {CurrencyManager} from "../../Utilities/CurrencyManager";
import {NumericUtilities} from "../../../FormBuilder/Utilities/NumericUtilities";
import {DashboardItem} from "../../DashboardItem";
import {SingleEvent} from "shared/core/Events/SingleEvent";


export class LineItemBase<p extends LineItemBaseProps=LineItemBaseProps,s extends LineItemBaseState=LineItemBaseState> extends React.Component<p, s> {
    static defaultProps: Partial<LineItemBaseProps> = {};
    public LinkNode:any;
    constructor(props: p) {
        super(props);
        this.state = {} as s;
        this.UpdateLink=this.UpdateLink.bind(this);
    }

    public render() {

        if(this.state.Mode=="removed")
            return <tr></tr>;
        return (
            <tr className={'rn-extra-product-item'}>
                {this.GetOptionName()}
                <td>
                    {this.GetValue()}
                </td>
                <td style={{textAlign:"right",minWidth:40}}>
                    {this.GetCost()}
                </td>
                <td style={{minWidth:40,textAlign:'right'}}>
                    {this.GetQty()}
                </td>
                {this.GetTotal()}
                {this.GetTaxes()}
                {this.CreateEditor()}
            </tr>
        )
    }



    public GetOptionName() {
        return (
            <td style={{textAlign:'left',paddingLeft:5}}>
                {this.props.Data.Label}
            </td>
        )
    }

    public GetValue() {
        if(this.state.Mode=='view')
            return this.props.Data.Value;
        else
            return <input style={{width:'100%'}} type={'text'} value={this.GetData('Value','')} onChange={x=>{this.props.Data.Value=x.target.value;this.forceUpdate();this.props.FieldUpdated.Publish()}}/>

    }

    public GetCost() {
        if(this.state.Mode=='view')
            return <span dangerouslySetInnerHTML={{__html:CurrencyManager.FormatPrice(this.props.Data.UnitPrice)}}/>
        else
            return <input style={{width:'100%',textAlign:'right',minWidth:70}} type={'number'} value={this.props.Data.UnitPrice} onChange={x=>{this.props.Data.UnitPrice=NumericUtilities.ParseNumber(x.target.value,0);this.RecalculateTotal();this.forceUpdate();this.props.FieldUpdated.Publish()}}/>


    }

    public GetQty() {

        if(this.state.Mode=='view')
            return this.props.Data.Quantity;
        else
            return <input style={{width:'100%',textAlign:'right',minWidth:70}} type={'number'} value={this.props.Data.Quantity} onChange={x=>{this.props.Data.Quantity=NumericUtilities.ParseNumber(x.target.value,0);this.RecalculateTotal();this.forceUpdate();this.props.FieldUpdated.Publish()}}/>
    }

    public GetTotal() {
        return(
            <td style={{textAlign:"right"}}  dangerouslySetInnerHTML={{__html:this.state.Mode=='view'?CurrencyManager.FormatPrice(this.props.Data.Price):''}}/>
        )
    }

    public FireValueChanged(){
        this.forceUpdate();
        this.props.FieldUpdated.Publish();
    }

    public GetTaxes() {
        return(
            <React.Fragment>
                {this.props.Data.Taxes.map(x=>
                    <td style={{textAlign:"right"}} key={x.Name} dangerouslySetInnerHTML={{__html:this.state.Mode=='view'?CurrencyManager.FormatPrice(x.Price):''}}/>
                )}
            </React.Fragment>
        )
    }

    public CreateEditor() {
        return (
            <td>
                <div  style={{display:'flex'}}>
                    {this.props.DashboardItem.props.IsEditable &&
                        <React.Fragment>
                            <span className="rnrowaction dashicons dashicons-edit" style={{fontWeight: 400, lineHeight: 1, fontSize: 16}} onClick={x => this.OnEdit()}/>
                            <span className="rnrowaction dashicons dashicons-no" style={{fontWeight:400,lineHeight:1,fontSize:16}} onClick={x=>{this.props.Data.Deleted=true;this.props.FieldUpdated.Publish();this.setState({Mode:'removed'},()=> jQuery(".button.calculate-action").trigger("click"))}}/>
                            <a ref={this.UpdateLink} className={'edit-order-item'} href={'#'} style={{display:"none"}}/>
                        </React.Fragment>
                    }
                </div>
            </td>
        )
    }

    public UpdateLink(node)
    {
        this.LinkNode=node;
    }

    public GetData<T=any>(propertyName:string,defaultValue:T):T
    {
        if(this.props.Data==null||this.props.Data[propertyName]==null)
            return defaultValue;

        return this.props.Data[propertyName];
    }

    private OnEdit() {
        this.setState({Mode: "edit"});
        jQuery(this.LinkNode).trigger('click');

    }

    private RecalculateTotal() {
        let unitPrice=NumericUtilities.ParseNumber(this.props.Data.UnitPrice,0);
        let quantity=NumericUtilities.ParseNumber(this.props.Data.Quantity,0);
        this.props.Data.Price=unitPrice*quantity;

    }

    public get DashboardItem(){
        return this.props.DashboardItem;
    }
}


//#region ----------------------------------------------------"State and props"------------------------------------------------------------------------------------
export interface LineItemBaseProps {
    Data:any;
    DashboardItem:DashboardItem;
    FieldUpdated:SingleEvent;
}

export interface LineItemBaseState {
    Mode:'view'|'edit'|'removed';
}

//#endregion "My Region"