import * as React from "react";
import {LineItemBase, LineItemBaseProps, LineItemBaseState} from "./LineItemBase";

export class SimpleImageLineItem extends LineItemBase<SimpleTextLineItemProps,SimpleTextLineItemState> {
    static defaultProps: Partial<SimpleTextLineItemProps> = {};

    constructor(props: SimpleTextLineItemProps) {
        super(props);
        this.state = {
            Mode:"view"
        };
    }

    GetValue(){
        if(this.state.Mode=='view')
            return (
                <div style={{width:'100%',textAlign:'center'}}>
                    <a href={this.props.Data.URL} target={'_blank'} style={{outline:'none',boxShadow:'none'}}>
                        <img style={{width:150,cursor:'zoom-in'}} src={this.props.Data.URL}/>
                    </a>
                    <label style={{display:'block'}}>{this.props.Data.Value}</label>
                </div>
            )
        else
            return(
                <div style={{width:'100%'}}>
                    <label style={{width:'100%',fontWeight:'bold'}}>URL</label>
                    <input style={{display:'block',width:'100%'}} type={'text'} value={this.props.Data.URL} onChange={x=>this.props.Data.URL=x.target.value}/>


                    <label style={{width:'100%',fontWeight:'bold',marginTop:5,display:'block'}}>Value</label>
                    <input style={{display:'block',width:'100%'}} type={'text'} value={this.props.Data.Value} onChange={x=>this.props.Data.Value=x.target.value}/>
                </div>
                )
    }
}


//#region ----------------------------------------------------"State and props"------------------------------------------------------------------------------------
interface SimpleTextLineItemProps extends LineItemBaseProps{

}

interface SimpleTextLineItemState extends LineItemBaseState{

}

//#endregion "My Region"