import * as React from "react";
import {OrderDashboardFactory} from "./OrderDashboardFactory";
import './Style.css';
import {DataInput} from "./Utilities/DataInput";
import {SingleEvent} from "shared/core/Events/SingleEvent";
export class DashboardItem extends React.Component<DashboardItemProps, DashboardItemState> {
    static defaultProps: Partial<DashboardItemProps> = {};
    public FieldUpdated:SingleEvent;
    constructor(props: DashboardItemProps) {
        super(props);
        this.state = {};
        this.FieldUpdated=new SingleEvent();
    }

    public render() {
        return (
            <tbody className={'extraProductDashboardItem'}>
                <tr style={{borderBottom:"none",backgroundColor:'#008ec2',color:'white'}}>
                    <td style={{borderBottom:"none"}} colSpan={6+this.props.Taxes.length}>
                        Product Options
                        <DataInput FieldUpdated={this.FieldUpdated} Data={this.props.Data} OrderLineId={this.props.OrderLineId}/>

                    </td>
                </tr>
                <tr style={{backgroundColor:'#008ec2',color:'white'}}>
                    <td>Option</td>
                    <td>Value</td>
                    <td>Cost</td>
                    <td>Qty</td>
                    <td>Total</td>
                    {this.props.Taxes.map((label,index)=>
                        <td key={index}>{label}</td>
                    )}
                    <td/>

                </tr>

                {this.props.Data.map(x=>
                    OrderDashboardFactory.GetDashboardItem(x,this,this.FieldUpdated)
                )}
            </tbody>
        )
    }
}


//#region ----------------------------------------------------"State and props"------------------------------------------------------------------------------------
interface DashboardItemProps {
    Data:any;
    OrderLineId:number;
    Nonce:string;
    IsEditable:boolean;
    Taxes:string[];
}

interface DashboardItemState {

}

//#endregion "My Region"