import * as React from "react";
import {LineItemBase, LineItemBaseProps, LineItemBaseState} from "./LineItemBase";


export class DateRangeLineItem extends LineItemBase<MultipleItemProps,MultipleItemState> {
    static defaultProps: Partial<MultipleItemProps> = {};

    constructor(props: MultipleItemProps) {
        super(props);
        this.state = {
            Mode:"view"
        };


    }


    GetValue(): any {
        let value=this.props.Data.Value;
        return (
            <div style={{display:'flex',flexWrap:"wrap",alignItems:'center'}}>
                <div>
                    {value.StartDateLabel.trim()!=''&&
                        <div><strong>{value.StartDateLabel}</strong></div>
                    }
                    {this.state.Mode=='view'?
                        <span>{value.StartValue}</span>:
                        <input onChange={x=>{value.StartValue=x.target.value;this.FireValueChanged();}} value={value.StartValue}/>
                    }
                </div>
                <div style={{margin:'0 5px'}}>-</div>
                <div>
                    {value.EndDateLabel.trim()!=''&&
                    <div><strong>{value.EndDateLabel}</strong></div>
                    }
                    {this.state.Mode=='view'?
                        <span>{value.EndValue}</span>:
                        <input value={value.EndValue} onChange={x=>{value.EndValue=x.target.value;this.FireValueChanged();}}/>
                    }
                </div>


            </div>
        )



    }
}


//#region ----------------------------------------------------"State and props"------------------------------------------------------------------------------------
interface MultipleItemProps extends LineItemBaseProps{
    SelectedValue:any;

}

interface MultipleItemState extends LineItemBaseState{

}

//#endregion "My Region"