import * as React from "react";
import {LineItemBase, LineItemBaseProps, LineItemBaseState} from "./LineItemBase";

declare let ajaxurl:string;
export class FileLineItem extends LineItemBase<SimpleTextLineItemProps,SimpleTextLineItemState> {
    static defaultProps: Partial<SimpleTextLineItemProps> = {};

    constructor(props: SimpleTextLineItemProps) {
        super(props);
        this.state = {
            Mode:"view"
        };
    }


    GetValue(): any {
        let columnNames=this.GetData('Columns',[{Name:''}]);
        let values=this.GetData('Value',[]);

        this.props.Data.Value=values;
        let rows:RowWrapper[]=values.map((item,index)=>new RowWrapper(index,item.Name,item.Path)) as RowWrapper[];
        return (
            <div>
                {rows.map((row,rowIndex)=>
                    <div key={row.Path}>
                        {this.GetContent(row)}
                    </div>

                )}

            </div>
        );

    }

    private GetColumnName(index: number, column: any[],length:number) {
        if(column.length>index&&column[index].Name.trim()!='')
        {
            let text= column[index].Name.trim();
            if(length>1)
                text+=' '+'#'+(parseInt(index.toString())+1);
            text+=':';

            return text;
        }

        return '';
    }

    private Remove(rowIndex: number) {
        if(this.props.Data.Value.length<rowIndex)
            return;

        this.props.Data.Value.splice(rowIndex,1);
        this.forceUpdate();
    }

    private GetContent(row: RowWrapper) {
        if(this.state.Mode=="view")
            return <a target={'_blank'} href={ajaxurl+'?action=rednaowooextraproduct_getfileupload&_nonce='+this.DashboardItem.props.Nonce+'&path='+encodeURI(row.Path)+'&name='+encodeURI(row.Name)}>{row.Name}</a>

        if(this.state.Mode=="edit")
            return <input style={{width:'100%'}} type={'text'} value={row.Path} onChange={x=>{row.Path=x.target.value;this.forceUpdate();this.props.FieldUpdated.Publish()}}/>
    }
}

class RowWrapper{


    constructor(public Id:number,public Name:string,public Path:string)
    {

    }
}

//#region ----------------------------------------------------"State and props"------------------------------------------------------------------------------------
interface SimpleTextLineItemProps extends LineItemBaseProps{

}

interface SimpleTextLineItemState extends LineItemBaseState{

}

