import * as React from "react";
import {LineItemBase, LineItemBaseProps, LineItemBaseState} from "./LineItemBase";


export class SimpleListLineItem extends LineItemBase<SimpleTextLineItemProps,SimpleTextLineItemState> {
    static defaultProps: Partial<SimpleTextLineItemProps> = {};

    constructor(props: SimpleTextLineItemProps) {
        super(props);
        this.state = {
            Mode:"view"
        };
    }


    GetValue(): any {
        let columnNames=this.GetData('Columns',[{Name:''}]);
        let values=this.GetData('Value',[]);

        this.props.Data.Value=values;
        let rows:RowWrapper[]=values.map((item,index)=>new RowWrapper(index,item)) as RowWrapper[];
        let hasHeaders=columnNames.some(x=>x.Name.trim()!='');
        return (
            <table>
                <tbody>
                {rows.map((row,rowIndex)=>
                    row.Columns.map((column,columnIndex)=>
                        <tr key={row.Id+'_'+columnIndex}>
                            {hasHeaders&&
                            <th style={{borderBottom:'none',padding:2}}>{this.GetColumnName(rowIndex,columnNames,rows.length)}</th>
                            }
                            <td style={{borderBottom:'none',padding:2}}>
                                {this.state.Mode=="view"?<span>{row.Columns[columnIndex]}</span>:
                                        <input type={'text'} style={{width:'100%'}} value={row.Columns[columnIndex]} onChange={x=>{row.Columns[columnIndex]=x.target.value;this.forceUpdate();this.props.FieldUpdated.Publish()}}/>
                                }

                            </td>
                            {columnIndex==0&&this.state.Mode=="edit"&&
                                <td rowSpan={row.Columns.length} style={{padding:2,verticalAlign:"middle"}}>
                                    <span className="rnrowaction dashicons dashicons-no" style={{fontWeight:400,lineHeight:1,fontSize:16,marginLeft:2}} onClick={x=>this.Remove(rowIndex)}/>
                                </td>
                            }
                        </tr>
                    )
                )}
                </tbody>
            </table>
        );

    }

    private GetColumnName(index: number, column: any[],length:number) {
        if(column.length>index&&column[index].Name.trim()!='')
        {
            let text= column[index].Name.trim();
            if(length>1)
                text+=' '+'#'+(parseInt(index.toString())+1);
            text+=':';

            return text;
        }

        return '';
    }

    private Remove(rowIndex: number) {
        if(this.props.Data.Value.length<rowIndex)
            return;

        this.props.Data.Value.splice(rowIndex,1);
        this.forceUpdate();
    }
}

class RowWrapper{
    Id:number;
    Columns:string[];

    constructor(id:number,columns:string[])
    {
        this.Id=id;
        this.Columns=columns;
    }
}

//#region ----------------------------------------------------"State and props"------------------------------------------------------------------------------------
interface SimpleTextLineItemProps extends LineItemBaseProps{

}

interface SimpleTextLineItemState extends LineItemBaseState{

}

//#endregion "My Region"