import {SimpleTextLineItem} from "./LineItems/Core/SimpleTextLineItem";
import {SimpleListLineItem} from "./LineItems/Core/SimpleListLineItem";
import {FileLineItem} from "./LineItems/Core/FileLineItem";
import {GroupLineItem} from "./LineItems/Core/GroupLineItem";
let React = require("react");
import {DashboardItem} from "./DashboardItem";
import {MultipleItems} from "./LineItems/Core/MultipleItems";
import {RepeaterLineItem} from "./LineItems/Core/Repeater/RepeaterLineItem";
import {ImagePickerLineItem} from "./LineItems/Core/ImagePickerLineItem";
import {DateRangeLineItem} from "./LineItems/Core/DateRangeLineItem";
import {SingleEvent} from "shared/core/Events/SingleEvent";

export class OrderDashboardFactory {
    public static GetDashboardItem(data:any,dashboardItem:DashboardItem,fieldUpdated:SingleEvent) {
        switch (data.Type) {
            case 'hidden':
            case 'textarea':
            case 'text':
            case 'masked':
            case 'datepicker':
                return <SimpleTextLineItem key={data.Id} DashboardItem={dashboardItem} Data={data} FieldUpdated={fieldUpdated}/>;
            case 'list':
                return <SimpleListLineItem key={data.Id} DashboardItem={dashboardItem} Data={data} FieldUpdated={fieldUpdated}/>

            case 'fileupload':
                return <FileLineItem key={data.Id} DashboardItem={dashboardItem} Data={data}  FieldUpdated={fieldUpdated}/>

            case 'grouppanel':
                return <GroupLineItem key={data.Id} DashboardItem={dashboardItem} Data={data} FieldUpdated={fieldUpdated}/>
            case 'checkbox':
            case 'dropdown':
            case 'radio':
                return <MultipleItems key={data.Id} DashboardItem={dashboardItem} Data={data} FieldUpdated={fieldUpdated}/>
            case 'repeater':
                return <RepeaterLineItem key={data.Id} DashboardItem={dashboardItem} Data={data} FieldUpdated={fieldUpdated}/>
            case 'imagepicker':
                return <ImagePickerLineItem key={data.Id} DashboardItem={dashboardItem} Data={data} FieldUpdated={fieldUpdated}/>
            case 'daterange':
                return <DateRangeLineItem key={data.Id} DashboardItem={dashboardItem} Data={data} FieldUpdated={fieldUpdated}/>
        }

        return null;
    }
}