import * as React from "react";
import {SingleEvent} from "shared/core/Events/SingleEvent";

export class DataInput extends React.Component<DataInputProps, DataInputState> {
    static defaultProps: Partial<DataInputProps> = {};

    constructor(props: DataInputProps) {
        super(props);
        this.state = {};
        this.props.FieldUpdated.Subscribe(this,()=>{this.RecalculateAllFields(this.props.Data as any);this.forceUpdate()})
    }

    public render() {
        return (
            <input type={'hidden'} value={JSON.stringify(this.props.Data)} name={'rnfields['+this.props.OrderLineId+']'}/>
        )
    }

    private RecalculateAllFields(itemList:any[]) {
        this.RemoveDeletedItems(itemList);


        for(let item of itemList)
        {
            if(item.Type=='grouppanel'||item.Type=='repeater')
            {
                this.RecalculateAllFields(item.Value);
                item.Price=0;
                for(let value of item.Value)
                {
                    if(value.Price==null)
                        continue;

                    let price=parseFloat(value.Price);
                    if(isNaN(price))
                        continue;

                    item.Price+=price;
                }
            }

            if(item.SelectedValues!=null)
            {
                this.RecalculateAllFields(item.SelectedValues);
                item.Price=0;
                for(let value of item.SelectedValues)
                {
                    if(value.Price==null)
                        continue;

                    let price=parseFloat(value.Price);
                    if(isNaN(price))
                        continue;

                    item.Price+=price;
                }
            }
        }
    }

    private RemoveDeletedItems(itemList: any[]) {
        for(let i=0;i<itemList.length;i++)
        {
            if(itemList[i].Type!=null&&(itemList[i].Type=='grouppanel'))
            {
                this.RemoveDeletedItems(itemList[i].Value);
                if(itemList[i].Value.length==0)
                {
                    itemList.splice(i,1);
                    i--;
                    continue;
                }
            }

            if(itemList[i].Type!=null&&(itemList[i].Type=='repeater'))
            {
                this.RemoveDeletedItems(itemList[i].Value);
                if(itemList[i].Value.length==0)
                {
                    itemList.splice(i,1);
                    i--;
                    continue;
                }
            }

            if(itemList[i].SelectedValues!=null)
            {
                this.RemoveDeletedItems(itemList[i].SelectedValues);
                if(itemList[i].SelectedValues.length==0) {
                    itemList.splice(i, 1);
                    i--;
                    continue;
                }
            }

            if(itemList[i].Deleted!=null&&itemList[i].Deleted===true)
            {
                itemList.splice(i,1);
                i--;
            }


        }
    }
}


//#region ----------------------------------------------------"State and props"------------------------------------------------------------------------------------
interface DataInputProps {
    FieldUpdated:SingleEvent;
    Data:any;
    OrderLineId:number;
}

interface DataInputState {

}

//#endregion "My Region"