import {SimpleTextLineItem} from "./LineItems/Core/SimpleTextLineItem";
import './Style.css';
let ReactDOM = require("react-dom");
let React = require("react");
import {CurrencyManager} from "./Utilities/CurrencyManager";
import {WpAjaxPost} from "shared/utilities/ajax/WPAjaxPost";
import {DashboardItem} from "./DashboardItem";

export function ProductLoader(order) {
    CurrencyManager.Initialize(order.Currency);
    let temp:HTMLDivElement = document.createElement("table");
    let container=null;

    WpAjaxPost.SetGlobalVar({_nonce:order.Nonce,_prefix:'rednaowooextraproduct'});
    ReactDOM.render(<DashboardItem Nonce={order.Nonce} OrderLineId={order.Id} Data={order.Data} IsEditable={order.IsEditable==true} Taxes={order.Taxes}/>,temp);
    ReplaceElement(temp,document.getElementById('rnExtraProductRow_'+order.Id))


};

function ReplaceElement(newElement:any,oldElement:any) {
    let child=newElement.firstChild;
    oldElement.parentNode.replaceChild(child,oldElement);
    return child;


}
