import * as React from "react";
import { LineItemBaseProps, LineItemBaseState} from "./LineItemBase";
import {OrderDashboardFactory} from "../../OrderDashboardFactory";
import {DashboardItem} from "../../DashboardItem";
import {SingleEvent} from "shared/core/Events/SingleEvent";

export class GroupLineItem extends React.Component<GroupLineItemProps> {
    static defaultProps: Partial<GroupLineItemProps> = {};

    constructor(props: GroupLineItemProps) {
        super(props);
        this.state = {
            Mode:"view"
        };
    }

    public render(): React.ReactElement<any, string | React.JSXElementConstructor<any>> | string | number | {} | React.ReactNodeArray | React.ReactPortal | boolean | null | undefined {
        return(
            <React.Fragment>
                {this.props.Data.Value.map(x=>
                    OrderDashboardFactory.GetDashboardItem(x,this.props.DashboardItem,this.props.FieldUpdated)

                )}
            </React.Fragment>
        )
    }

}


//#region ----------------------------------------------------"State and props"------------------------------------------------------------------------------------
interface GroupLineItemProps extends LineItemBaseProps{
    Data:any;
    DashboardItem:DashboardItem;
    FieldUpdated:SingleEvent;
}

interface SimpleTextLineItemState extends LineItemBaseState{

}

//#endregion "My Region"