import * as React from "react";
import {LineItemBase, LineItemBaseProps, LineItemBaseState} from "./LineItemBase";
import {SimpleTextLineItem} from "./SimpleTextLineItem";


export class MultipleItems extends LineItemBase<MultipleItemProps,MultipleItemState> {
    static defaultProps: Partial<MultipleItemProps> = {};

    constructor(props: MultipleItemProps) {
        super(props);
        this.state = {
            Mode:"view"
        };


    }


    render(): any {
        return (
            <React.Fragment>
                {this.props.Data.SelectedValues.map(x=>
                    <SimpleTextLineItem key={x.Id} Data={x} DashboardItem={this.props.DashboardItem} FieldUpdated={this.props.FieldUpdated}/>

                )}
            </React.Fragment>
        )
    }
}


//#region ----------------------------------------------------"State and props"------------------------------------------------------------------------------------
interface MultipleItemProps extends LineItemBaseProps{
    SelectedValue:any;

}

interface MultipleItemState extends LineItemBaseState{

}

//#endregion "My Region"